import React from "react";

import "../stylesheet/default.css";
import LoadingIcon from "../images/loading-icon.svg";
import InformationIcon from "../images/information-icon.svg";

export default function Information(props) {
	return (
		<div
			style={{
				animation: "",
				marginLeft: "30px",
			}}
			className={props.className}
		>
			<img
				style={{ verticalAlign: "middle" }}
				src={LoadingIcon}
				alt="Loading..."
				width="30px"
				height="30px"
			></img>
			<img
				style={{ verticalAlign: "middle", transform: "translateX(-125%)" }}
				src={InformationIcon}
				alt="Information"
				width="20px"
				height="20px"
			></img>
			<span style={{ verticalAlign: "middle", color: "white" }}>
				{props.children}
			</span>
		</div>
	);
}
