import React, { Component } from "react";
import BubbleBox from "../components/BubbleBox";
import Information from "../components/Information";

const repositoryURL = "https://api.github.com/users/kvintanaka/repos";

class CurrentProjects extends Component {
	constructor() {
		super();
		this.ready = false;
		this.rendered = [];

		this.fetchProjects();
	}

	fetchProjects() {
		fetch(repositoryURL)
			.then((response) => response.json())
			.then((projects) =>
				projects.forEach((project) => this.buildRendered(project))
			)
			.then(() => (this.ready = true))
			.then(() => this.forceUpdate());
	}

	buildRendered(project) {
		this.rendered.push(
			<BubbleBox className="align-middle-each">
				<img
					src="https://upload.wikimedia.org/wikipedia/commons/9/91/Octicons-mark-github.svg"
					alt="Github Repository"
				/>
				<span>
					<a href={project.html_url}>{project.full_name}</a>
					<p id="DESCRIPTION">{project.description}</p>
				</span>
			</BubbleBox>
		);
	}

	render() {
		return this.ready ? (
			<div>
				<div>{this.rendered}</div>
			</div>
		) : (
			<div>
				<Information>Fetching live data from Github REST API</Information>
			</div>
		);
	}
}

export default CurrentProjects;
