import React, { Component } from "react";
import Parser from "html-react-parser";

import "../stylesheet/wordpress.css";

import BubbleBox from "../components/BubbleBox";

class Article extends Component {
	constructor(props) {
		super(props);
		this.rendered = [];
		this.ready = false;
		this.fetchArticle();
	}

	fetchArticle() {
		const api = "https://wordpress.kvintanaka.com/wp-json/wp/v2/posts/?slug=";
		fetch(api + this.props.match.params.slug)
			.then((response) => response.json())
			.then((articles) =>
				articles.forEach((article) => this.buildRendered(article))
			)
			.then(() => (this.ready = true))
			.then(() => this.forceUpdate());
	}

	buildRendered(article) {
		this.rendered.push(
			<BubbleBox className="side-ads">
				<h1>{article.title.rendered}</h1>
				<div>{Parser(article.content.rendered)}</div>
			</BubbleBox>
		);
	}

	render() {
		return this.ready ? (
			<div className="article">{this.rendered}</div>
		) : (
			<div>
				<img
					src="https://upload.wikimedia.org/wikipedia/commons/9/98/WordPress_blue_logo.svg"
					alt="Waiting..."
					height="50px"
					weight="50px"
					style={{
						filter: "brightness(1000%)",
						position: "absolute",
						top: "50%",
						left: "50%",
						transform: "translateY(-50%) translateX(-50%)",
					}}
				/>
			</div>
		);
	}
}

export default Article;
