import React, { Component } from "react";
import { NavLink } from "react-router-dom";

import BubbleBox from "../components/BubbleBox";
import Information from "../components/Information";

class Articles extends Component {
	constructor() {
		super();
		this.ready = false;
		this.rendered = [];

		this.fetchArticles();
	}

	fetchArticles() {
		const api =
			"https://wordpress.kvintanaka.com/wp-json/wp/v2/posts/?_fields[]=title&_fields[]=slug";
		fetch(api)
			.then((response) => response.json())
			.then((articles) =>
				articles.forEach((article) => this.buildRendered(article))
			)
			.then(() => (this.ready = true))
			.then(() => this.forceUpdate());
	}

	buildRendered(article) {
		this.rendered.push(
			<BubbleBox className="align-middle-each half-page">
				<img
					src="https://upload.wikimedia.org/wikipedia/commons/9/98/WordPress_blue_logo.svg"
					alt="Wordpress Articles"
				/>
				<span>
					<NavLink to={"/article/" + article.slug}>
						{article.title.rendered}
					</NavLink>
				</span>
			</BubbleBox>
		);
	}

	render() {
		return this.ready ? (
			<div>
				<div>{this.rendered}</div>
			</div>
		) : (
			<div>
				<Information>
					<span>Fetching live data from </span>
					<a
						className="no-url-underline"
						href="https://wordpress.kvintanaka.com"
					>
						Wordpress
					</a>
					<span> REST API</span>
				</Information>
			</div>
		);
	}
}

export default Articles;
